body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  height: 100vh;
  background-color: #5f96e9;
}

.App {
  text-align: center;
}

.modeButton {
  margin: 30px 3px;
  font-size: 1.8em;
  background-color: #b1a7f3;
  padding: 5px;
  padding: 5px 10px;
  border-radius: 10px;
}
.modeButton.active {
  background-color: #d64444;
}

.operationWrapper {
  display: flex;
  /* margin: 40px; */
  justify-content: center;
  align-items: center;
}

.operation {
  display: flex;
  align-items: baseline;
  justify-content: center;
  font-size: 5rem;
  font-weight: bold;
  letter-spacing: 2px;
  border: 5px solid #9ecfea;
  background-image: url(/portfolio/projects/calculationsKids/static/media/sign2.9b6050a9.jpg);
  height: 65vh;
  width: 45vw;
  background-position-y: center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 3px 3px 8px 2px #666666;
}

.operation.wrongAnswer {
  border-color: red;
}

.operation input {
  width: 180px;
  font-size: inherit;
  color: #151675;
  border: none;
  text-align: left;
  font-weight: bold;
  margin: 0 0 0 25px;
  background-color: transparent;
}

@media only screen and (min-width: 1400px) {
  .operation {
    font-size: 8rem;
  }
}

.operation p {
  background-color: transparent; /*#bba187; */
  margin: 40vh 0 0 50px;
}

.numPad {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 20vw;
  margin-left: 60px;
}

.numPad button {
  width: 6vw;
  height: 6vw;
  font-size: 3.5em;
  font-weight: bold;
  border: 1px solid #777;
  border-radius: 10px;
  margin: 1px;
  background-color: #fff;
}

.modepick {
  margin-top: 300px;
  font-size: 6em;
}

.starsContainer {
  width: 80%;
  margin: 40px auto 0;
  height: 50px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.star {
  width: 50px;
  height: 50px;
  /* background-image: url('./grey-star-hi.png'); */
  background-size: cover;
}

.star.not-set {
  background-image: url(/portfolio/projects/calculationsKids/static/media/grey-star-hi.1e0fb0c1.png);
}

.star.correct {
  background-image: url(/portfolio/projects/calculationsKids/static/media/green-star-hi.e7d7787e.png);
}

.star.false {
  background-image: url(/portfolio/projects/calculationsKids/static/media/red-star-hi.99409184.png);
}

.resultsContainer {
  width: 90%;
  margin: 10px auto;
  display: flex;
}
.results {
  margin-top: 300px;
  font-size: 3em;
  flex-grow: 1;
}

.correctAnswers {
  color: #960d0d;
  font-weight: bold;
}

.resultsTable {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-content: center;
  margin-top: 50px;
  font-size: 1.5em;
  text-align: left;
}

.resultsTable p {
  padding: 5px;
  margin: 0;

  border: 1px solid #271079;
}

.resultRowSection {
  margin-left: 20px;
}

.resultRowSection.correct {
  color: green;
}
.resultRowSection.wrong {
  color: red;
}

